/* sidebar.css */

  .sidebar {
    height: 100%; /* Set sidebar height to 100% */
    width: 250px; /* Set sidebar width */
    background-color: white; /* Sidebar background color */
    position: absolute;
    top: 0;
    left: 0;
    transition: transform 0.3s ease; /* Smooth transition for show/hide */
    z-index: 100;

  }

  .sidebar.closed {
    transform: translateX(-100%); /* Hide sidebar */
  }

.sidebar-header {
    display: flex;
    align-items: center;
    padding: 10px;
    background-color: #ffffff;
}
.app-container.sidebar-open {
    margin-left: 250px; /* Adjust if needed */
    transition: margin-left 0.3s ease;
  }
.sidebar-logo {
    width: 170px;
    height: 40px;
    margin: 10px auto;
}

.toggle-button {
    background: none;
    border: none;
    cursor: pointer;
    position: absolute;
    top: 10px;
    right: -50px;
}

.toggle-button img {
    width: 40px;
    height: 40px;
}

.sidebar-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    background-color: #ffffff;
}

.sidebar-button {
    background-color: #4f2c1d;
    color: #4f2c1d;
    border: 2px solid #4f2c1d;
    padding: 10px 20px;
    margin: 10px 0;
    cursor: pointer;
    font-size: 16px;
    width: 80%;
    text-align: center;
    transition: background-color 0.3s ease, color 0.3s ease;
}

.sidebar-button:hover {
    background-color: #4f2c1d;
    color: #ffffff;
}

.sidebar-button.new-scenario-button,
.sidebar-button.model-history-button {
    background-color: #ffffff;
    color: black;
    border: 2px solid white;
}

.sidebar-button.new-scenario-button:hover,
.sidebar-button.model-history-button:hover {
    background-color: #352924;
    color: white;
}
.sidebar-button.active {
    background-color: #352924; /* The color you want for the active button */
    color: white;
    border: 2px solid #352924; /* Optional: match the background color */
}