.model-history {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    width: 100%;
    height : 100%;
    display: flex;
    justify-items: center;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
  }
#root{
    height: 100%;
}
html{
    height: 100%;
}
.header {
    display: flex;
    gap: 10px;
    align-items: center;
    align-self: self-start;
    padding-left: 60px;
    padding-top: 10px;
    padding-bottom: 10px;
  }
.scenarios-list-container{
    width: 90%;
    display: flex;
    flex-direction: column;
    border: 1px solid #E3E6EB;
    background-color: white;
    justify-content: center;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
}
.scenarios-list-heading-and-search{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 89%;
    padding-bottom: 10px;
}
.scenarios-list-heading h2{
font-size: 16px;
    font-weight: 600;
    color: #F07300;
}
body{
    margin: 0px;
    height: 100%;
}
.header h1 {
    font-size: 16px;
    font-weight: 600;
    /* margin: 0; */
}
  
  
  .scenario-table {
    width: 89%;
    border-collapse: collapse;
  }
  
  .scenario-table th, .scenario-table td {
    padding: 8px;
    text-align: left;
  }
  
  .scenario-table th {
    background-color: #f2f2f2;
  }
  
  .file-name {
    display: flex;
    align-items: center;
  }
  
  .file-icon {
    width: 20px;
    height: 20px;
    margin-right: 8px;
  }
  .search-icon{
    width: 20px;
    height: 20px;
    position: absolute;
    left: 5px;
    pointer-events: none;
  }
  .model-history-icon{
    width: 20px;
    height: 20px;
  }
  .status {
    display: flex;
    align-items: center;
  }
  
  .status-icon {
    width: 20px;
    height: 20px;
    margin-right: 8px;
  }
  
  .status.completed {
    color: green;
  }
  
  .status.in-progress {
    color: orange;
  }
  
  .status.failed {
    color: red;
  }
  
  .view-output-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
  }
  
  .view-output-button img {
    width: 20px;
    height: 20px;
  }
  .search-container {
        position: relative;
        display: flex;
        align-items: center;
        width: 40%;
  }
  
  .search-input {
    padding-left: 30px;
    padding-top: 5px;
    padding-bottom: 5px;/* Adjust as needed to make room for the icon */
    width: 100%;
    align-content: space-between;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  .pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }
  
  .pagination button {
    background-color: #f4f4f4;
    color: #cccccc;
    border: none;
    padding: 10px;
    margin: 0 5px;
    cursor: pointer;
  }
  
  .pagination button:disabled {
    background-color: white;
    cursor: not-allowed;
  }
  
  .pagination span {
    margin: 0 10px;
  }
  
  .view-output-button:disabled{
    cursor: not-allowed;
    color:#cccccc
  }
  